/* 流程图查看样式 */
.process-flow-container {
  position: relative;
  transition: all 0.3s ease;
  max-height: 300px;
  overflow: hidden;
  cursor: zoom-in;
}

.process-flow-container.expanded {
  max-height: none;
  cursor: zoom-out;
}

.process-flow-container img {
  width: 100%;
  height: auto;
  object-fit: cover;
  transition: all 0.3s ease;
}

.process-flow-container:not(.expanded) img {
  max-height: 300px;
  object-position: top;
}

.process-flow-container.expanded img {
  transform: scale(1);
}

.process-flow-container:not(.expanded)::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  height: 40px;
  background: linear-gradient(to bottom, transparent, rgba(255, 255, 255, 0.9));
  pointer-events: none;
}

.process-flow-container:not(.expanded):hover {
  box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05);
}

.process-flow-container:not(.expanded):hover img {
  transform: scale(1.02);
}

.expand-hint {
  position: absolute;
  bottom: 10px;
  left: 50%;
  transform: translateX(-50%);
  background-color: rgba(255, 255, 255, 0.9);
  padding: 4px 12px;
  border-radius: 9999px;
  font-size: 0.75rem;
  color: #6b7280;
  opacity: 0;
  transition: opacity 0.3s ease;
  pointer-events: none;
  white-space: nowrap;
}

.process-flow-container:not(.expanded):hover .expand-hint {
  opacity: 1;
} 

