  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1);
  border: 1px solid #f3f4f6;
}

.knowledge-title {
  font-size: 1.25rem; /* 增大标题字体 */
  font-weight: bold;
  color: #1e40af;
  margin-bottom: 1.25rem;
  display: flex;
  align-items: center;
}

.knowledge-list {
  display: flex;
  flex-direction: column;
  gap: 0.75rem;
}

.knowledge-item {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.knowledge-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.knowledge-name {
