  font-weight: bold;
  color: #166534;
  margin-bottom: 1.25rem;
  display: flex;
  align-items: center;
}

.skills-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 0.75rem;
}

.skill-item {
  display: flex;
  align-items: center;
  padding: 0.75rem;
  background: linear-gradient(135deg, #f0fdf4, #ecfdf5);
  border-radius: 0.75rem;
  border: 1px solid #bbf7d0;
  transition: all 0.3s ease;
