  align-items: center;
  justify-content: center;
}

.skill-name {
  font-size: 0.875rem; /* 增大技能名称 */
  font-weight: 500;
  color: #166534;
  line-height: 1.25;
  flex: 1;
  display: flex;
  align-items: center;
}

/* 职业发展路径 */
.career-path-card {
  background: white;
  border-radius: 1rem;
  padding: 1.5rem;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1);
  border: 1px solid #f3f4f6;
}

.career-path-title {
  font-size: 1.25rem;
  font-weight: bold;
  color: #1f2937;
  margin-bottom: 1.25rem;
  display: flex;
  align-items: center;
}

.career-path-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.career-stage {
  text-align: center;
}

.career-circle {
  width: 3rem;
  height: 3rem;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 0.5rem;
  transition: all 0.3s ease;
}

.career-circle:hover {
  transform: scale(1.1);
}

.career-circle.junior {
  background: linear-gradient(135deg, #3b82f6, #2563eb);
}

.career-circle.intermediate {
  background: linear-gradient(135deg, #10b981, #059669);
}

.career-circle.senior {
  background: linear-gradient(135deg, #8b5cf6, #7c3aed);
}

.career-circle-text {
  color: white;
  font-weight: bold;
  font-size: 0.875rem;
}

.career-stage-name {
  font-size: 0.75rem;
  color: #6b7280;
}

.career-arrow {
  width: 1rem;
  height: 1rem;
  color: #9ca3af;
  transition: all 0.3s ease;
}

.career-stage:hover + .career-arrow {
  color: #6b7280;
  transform: translateX(4px);
}

/* 响应式设计 */
@media (max-width: 640px) {
  .responsibilities-container {
    padding: 1rem;
  }
  
  .skills-grid {
    grid-template-columns: 1fr;
  }
  
  .career-path-container {
    flex-direction: column;
    gap: 1rem;
  }

  .career-stage {
    width: 100%;
    flex-direction: row;
    justify-content: center;
    gap: 1rem;
  }

  .career-arrow {
    transform: rotate(90deg);
  }

  .career-stage:hover + .career-arrow {
    transform: rotate(90deg) translateX(4px);
  }

  .career-stage-name {
    font-size: 0.875rem;
    min-width: 80px; /* 确保文字有足够空间 */
    text-align: left;
  }
} 

/* ME模块样式 */
.me-content {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}

/* 今日概览卡片 */
.overview-card {
  background: linear-gradient(135deg, #3b82f6, #9333ea);
  border-radius: 1rem;
  padding: 1.5rem;
  color: white;
  box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05);
  position: relative;
  overflow: hidden;
}

.overview-card::before {
  content: '';
  position: absolute;
  top: 0;
  right: 0;
  width: 128px;
  height: 128px;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 50%;
  transform: translate(64px, -64px);
}

.overview-card::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 96px;
  height: 96px;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 50%;
  transform: translate(-48px, 48px);
}

.overview-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 1rem;
  position: relative;
  z-index: 1;
}

.overview-title {
  font-size: 1.125rem;
  font-weight: 700;
}

.overview-icon {
  width: 1.5rem;
  height: 1.5rem;
  opacity: 0.8;
}

.overview-stats {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 1rem;
  position: relative;
  z-index: 1;
}

.stat-item {
  text-align: center;
  padding: 0.75rem;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 0.75rem;
  backdrop-filter: blur(4px);
  transition: transform 0.3s ease;
}

.stat-item:hover {
  transform: translateY(-2px);
  background: rgba(255, 255, 255, 0.2);
}

.stat-number {
  font-size: 1.5rem;
  font-weight: 700;
  margin-bottom: 0.25rem;
  color: white;
}

.stat-label {
  font-size: 0.875rem;
  color: white; /* 修改为和数字相同的颜色 */
  font-weight: 500; /* 增加字重使其更清晰 */
}

/* 待办事项卡片 */
.todo-card {
  background: white;
  border-radius: 1rem;
  padding: 1.5rem;
