}

/* KPI指标卡片 */
.kpi-container {
  background-color: white;
  border-radius: 0.75rem;
  padding: 1.5rem;
  box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.05);
}

.kpi-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 1.5rem;
}

.kpi-title {
  font-size: 1.375rem; /* 增大 KPI 标题字体 */
  font-weight: 600;
  display: flex;
  align-items: center;
}

.kpi-title-icon {
  width: 1.25rem;
  height: 1.25rem;
  margin-right: 0.5rem;
  color: #16a34a;
}

.kpi-badge {
  font-size: 0.875rem; /* 略微增大徽章字体 */
  color: #6b7280;
  background-color: #f3f4f6;
  padding: 0.25rem 0.75rem; /* 增加内边距使徽章更大 */
