.kpi-item {
  position: relative;
}

.kpi-card {
  padding: 1.25rem;
  border-radius: 0.75rem;
  border: 2px solid;
  transition: all 0.3s ease;
}

.kpi-card:hover {
  box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05);
  transform: translateY(-0.25rem);
}

/* 成功状态样式 */
.kpi-card-success {
  border-color: #bbf7d0;
  background: linear-gradient(90deg, #f0fdf4 0%, #ecfdf5 100%);
}

.kpi-card-success:hover {
  border-color: #86efac;
}

/* 警告状态样式 */
.kpi-card-warning {
  border-color: #fef3c7;
  background: linear-gradient(90deg, #fffbeb 0%, #fef7ed 100%);
}

.kpi-card-warning:hover {
  border-color: #fde68a;
}

